@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}

body{
  font-family: 'Rubik', sans-serif;
  font-weight: 400;
  font-size: 1rem;
  color: azure;
  position: relative;
  min-height: 100vh;
}






.background-pattern{
    background: url(/static/media/pattern-bg.a60a3c85.png) bottom center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    height: 40vh;
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
}

.map-container{
    
    position: absolute;
    width: 100%;
    height: 60vh;
    bottom: 0;
    left: 0;
    
}

.container{
    max-width: 1200px;
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
}

header{
    margin: 2em 0;
}

.title{
    color: #fff;
    font-weight: 500;
    font-size: 1.725rem;
}

.form{
    margin-bottom: 2em;
    width: 100%;
    box-shadow: 10px 10px 35px rgba(0,0,0, .28);
}

#search-ip, .submit{
    padding: 1em 1.5em;
    font-size: 1rem;
}


#search-ip{
    font-family: 'Rubik', sans-serif;
    font-weight: 500;
    border: none;
    border-bottom-left-radius: 15px;
    border-top-left-radius: 15px;
}

#search-ip::-webkit-input-placeholder{
   color: hsl(0, 0%, 59%);
   font-weight: 400;
}

#search-ip:-ms-input-placeholder{
   color: hsl(0, 0%, 59%);
   font-weight: 400;
}

#search-ip::placeholder{
   color: hsl(0, 0%, 59%);
   font-weight: 400;
}

.submit{
    cursor: pointer;
}

.submit:focus, .submit:hover{
    background-color: rgb(34, 34, 34);
}

@media(min-width: 900px){
    
    body{
        font-size: 1.2rem;
    }

    .title{
        font-size: 2.4rem;
    }
    header{
        margin: 3em 0;
    }

    #search-ip{
        width: 500px;
    }
}

.submit{
    border: 1px solid #000;
    background-color: #000;
    color: #fff;
    font-weight: 700;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
}





.card{
    z-index: 2;
    background-color: #fff;
    color: hsl(0, 0%, 17%);
    padding: 1.5em 4em;
    text-align: center;
    border-radius: 15px;
    box-shadow: 10px 10px 35px rgba(0,0,0, .28);
}

.label{
    font-size: .625rem;
    text-transform: uppercase;
    letter-spacing: 1.3px;
    font-weight: 700;
    color: hsl(0, 0%, 59%);
    margin-bottom: .5em;
}

.data{
    font-weight: 700;
    margin-bottom: 1em;
}

.data:last-child{
    margin-bottom: 0;
}

@media(min-width: 900px){
    .card{
        display: flex;
        /* gap: 1em; */
        padding: 2em;
    }

    .col{
        text-align: left;
        padding: 0 3em 0 1em;
        border-right: 1px solid hsl(0, 0%, 59%);
        
    }

    .col:last-child{
        border: none;
    }
}
